@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Rubik', sans-serif;
  
}
header img{
  width: 10%;
}
main{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
main img{
width: 43%;
height: 70;
}
.content{
  width: 30%;
  padding: 2em;
  margin-bottom: auto;
}



.wrapper{
  margin-right: auto;
    padding: 2em;
   
    width: 100vw;
}

 h2{
    
    
    color: #0e0f0f;
    padding: 0.1rem;
    font-weight: 300;
    font-size: 1.5rem;
    margin: 0;
    letter-spacing: 0.1rem;
    text-decoration:underline;
    -webkit-text-decoration-color: rgb(243, 247, 3);
            text-decoration-color: rgb(243, 247, 3);
    
}

 h4{
    
    color: #181816;
    padding: 1rem;
    font-weight: 140;
    font-size: 1rem;
    margin: 0rem;
    text-decoration:underline;
    -webkit-text-decoration-color: rgb(243, 247, 3);
            text-decoration-color: rgb(243, 247, 3);
}
form{
    padding: 1rem;
    border-radius: 3rem;
    background: white;
    box-shadow: 2px 2px 2px 2px inset white;
    width: 100vw;
    height: 600px;
    
}

button,input,.name, .dob{
    padding: 0.5rem;
    border-radius: 1rem;
    margin: 4px;

}

label{
    padding: 0.5rem;
    font-weight: 50;
    font-size: small;

}
select{
    border-radius: 1rem;
    padding: 0.2rem;
}

@media screen and (max-width:768px){
    .dob{
        
        padding: 0.2rem;
        border-radius: 1rem;
        margin: 4px;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-flex-direction: row;
                flex-direction: row;
        position: relative;
    
    }
    h2{
    
    
      color: #0e0f0f;
      padding: 0.1rem;
      font-weight: 200;
      font-size: 1.1rem;
      margin: 0;
      letter-spacing: 0.1rem;
      text-decoration:underline;
      -webkit-text-decoration-color: rgb(243, 247, 3);
              text-decoration-color: rgb(243, 247, 3);
      
  }
  .wrapper{
    margin-right: auto;
      padding: 0em;
     margin-bottom: auto;
      height: -webkit-min-content;
      height: min-content;
  }
  
  
}
